@import "../main.scss";

@include to-tablet-portrait {
    .card {
        max-width: 18rem;
        box-shadow: 0px 0px 20px 0px $light-grey;
        border-radius: 1rem;
        &-body {
            text-align: center;
        }
        &-content {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        &-title {
            margin-top: 1rem;
        }

        &-img {
            max-width: 17rem;
        }
        &-terms {
            margin-top: 0.5rem;
            font-size: $font-extra-smallest;
        }
    }
}
